const Base= {};

//
Base.ready=function(url,data,view,title){
	view.$loading({text: '..'});
	console.log('--------------------'+title+'--------------------');
	console.log('url:'+url);
	console.log(data);
	console.log('--------------------'+title+'--------------------');
}

//
Base.error=function(view,title){
	console.log('--------------------'+title+'--------------------');
	console.log("")
	console.log('--------------------'+title+'--------------------');
	view.$loading().close();
	view.$message.error('');
//	view.$alert("", '')
}

//
Base.success=function(res, view,title){
	console.log('--------------------'+title+'--------------------');
	console.log("")
	console.log(res);
	console.log('--------------------'+title+'--------------------');
	view.$loading().close();
        view.$message({
          message: '',
          type: 'success'
        });
}


//
Base.encrypt=function(){
	function Zero(){
		
	}
	function Solo(){
		
	}
	function Duet(){
		
	}
	function Trio(){
		
	}
	function Quartet(){
		
	}
	this.encrypt=function(){
		let arg=arguments;
		let len=arg.length;
		switch(len){
			case 0:
				return Zero();
			case 1:
				return Solo();
			case 2:
				return Duet();
			case 3:
				return Trio();
			case 4:
				return Quartet();
			default:
				return '';
		}
		
	}
}

export default Base;






