const Tool = {};
/**
 * ajax
 * @param {object} mySetting ajax
 */
Tool.ajax = function (mySetting) {
        var setting = {
            url: window.location.pathname, //ajax
            async: true, //true false
            type: 'GET', //
            data: {}, //
            dataType: 'json',
            view:'',
            title:'',
            success: function (text,view) { }, //
            error: function (view,title) { } //
        };


    var aData = []; //
    var sData = ''; //
    //
    for (var attr in mySetting) {
        setting[attr] = mySetting[attr];
    }

    setting.type = setting.type.toUpperCase();
	
    var xhr = new XMLHttpRequest();
    try {
        if (setting.type == 'GET') { //get
		    for (var attr in setting.data) {
		        aData.push(attr + '=' + filter(setting.data[attr]));
		    }
			
		    sData = aData.join('&');
            sData = setting.url + '?' + sData;
            xhr.open(setting.type, sData + '&' + new Date().getTime(), setting.async);
            xhr.send();
        } else if(setting.type=='POST'){ //post
			    for (var attr in setting.data) {
			        aData.push(attr + '=' + filter(setting.data[attr]));
			    }
			    sData = aData.join('&');
	            xhr.open(setting.type, setting.url, setting.async);
	            xhr.setRequestHeader("Content-type", "application/x-www-form-urlencoded");
	            xhr.send(sData);
            
        }
    } catch (e) {
        return httpEnd();
    }

    if (setting.async) {
        xhr.addEventListener('readystatechange', httpEnd, false);
    } else {
        httpEnd();
    }

    function httpEnd() {
        if (xhr.readyState == 4) {
            var head = xhr.getAllResponseHeaders();
            var response = xhr.responseText;
            //json

            if (/application\/json/.test(head) || setting.dataType === 'json' && /^(\{|\[)([\s\S])*?(\]|\})$/.test(response)) {
                response = JSON.parse(response);
            }

            if (xhr.status == 200) {
                setting.success(response,setting.view, setting, xhr);
            } else {
                setting.error(setting.view,setting.title,setting, xhr);
            }
        }
    }
    xhr.end = function () {
        xhr.removeEventListener('readystatechange', httpEnd, false);
    }

    function filter(str) { //
        str += ''; //
        str = str.replace(/%/g, '%25');
        str = str.replace(/\+/g, '%2B');
        str = str.replace(/ /g, '%20');
        str = str.replace(/\//g, '%2F');
        str = str.replace(/\?/g, '%3F');
        str = str.replace(/&/g, '%26');
        str = str.replace(/\=/g, '%3D');
        str = str.replace(/#/g, '%23');
        return str;
    }
    return xhr;
};
/**
 * ajax post
 * @param {string} pathname 
 * @param {object} data     
 * @param {function} success  
 * @param {function} error    
 */
Tool.post = function (pathname, data,view,success,error,title) {
    var setting = {
        url: pathname, //ajax
        type: 'POST', //
        data: data, //
        view:view,
        title:title,
        success: success || function () { }, //
        error: error || function () { } //
    };
    return Tool.ajax(setting);
};
/**
 * ajax get
 * @param {string} pathname 
 * @param {object} data     
 * @param {function} success  
 * @param {function} error    
 */
Tool.get = function (pathname, data,view,success, error) {
    var setting = {
        url:  pathname, //ajax
        type: 'GET', //
        data: data, //
        view:view,
        success: success || function () { }, //
        error: error || function () { } //
    };
    return Tool.ajax(setting);
};



///////////////////////////////////////////////////////////////////////

Tool.validIsUser=function(){
	//cookies
	if(Tool.getCookie("userId")==null||Tool.getCookie("userId")==''){
		return false;
	}
	return true ;
};

export default Tool;


