<script>
	
export default {
  name: 'admin',
  data () {
    return {
        form: {
			name:'',//
			sex:'',//
			identityNum:'',//
			nation:'',//
			birthday:'',//
			isMarry:'',//
			politicalIdentity:'',//
			speciality:'',//
			householdAddress:'',//
			householdType:'',//
			applyJob:'',//
			job:'',//
			major:'',//
			phoneNum:'',//
			email:'',//
			address:'',//
			height:'',//
			weight:'',//
			emergencyContact:'',//
			emergencyPhone:'',//
			inputTime:'',//
			img:'',//
			arrivalTime:'',//
			isAgree:true,//
        },
    	//
    	rules: {
    		name:[ { required: true, message: '', trigger: 'blur' }],
    		sex:[{ required: true, message: '', trigger: 'blur' }], 
    		identityNum:[ { required: true, message: '', trigger: 'blur' }],
    		birthday:[ { required: true, message: '', trigger: 'blur' }],
    		nation:[ { required: true, message: '', trigger: 'blur' }],
    		isMarry:[ { required: true, message: '', trigger: 'blur' }],
    		politicalIdentity:[ { required: true, message: '', trigger: 'blur' }],
//    		speciality:[ { required: true, message: '', trigger: 'blur' }],
      		householdType:[ { required: true, message: '', trigger: 'blur' }],
      		applyJob:[ { required: true, message: '', trigger: 'blur' }],
      		phoneNum:[ { required: true, message: '', trigger: 'blur' }],
      		email:[ { required: true, message: '', trigger: 'blur' }],
      		address:[ { required: true, message: '', trigger: 'blur' }],
    	},
        //
        education: [
        	//{personId:1,schoolName:"",education:"",major:"",certificate:"",timeSlot:"2013-2017"}
        ],
        //
        family:[
        	//{personId:1,name:"",birthday:"19950101",workCompany:"",job:"",relationship:""}
        ],
        //
        train: [
       		//{personId:1,trainName:"",company:"",certificate:"",timeSlot:"2013-2017"}
        ],
        //
        workExperience:[
        	//{personId:1,timeSlot:"2013-2017",workCompany:"",job:"",reasons:"",pay:"",consultantName:"",consultantNum:"18314345458"}
        ],
        //
        consultant:[
       		//{personId:1,name:"",workCompany:"",job:"",phoneNum:"18314345458"},
        ],
        //
		frame:{
			education:false,
			family:false,
			train:false,
			consultant:false,
			workExperience:false,
		},
		//
		educationForm:{schoolName:"",education:"",major:"",certificate:"",timeSlot:""},
		//{schoolName:"",education:"",major:"",certificate:"",timeSlot:"2013-2017"},
		familyForm:{name:"",birthday:"",workCompany:"",job:"",relationship:""},
		//{name:"",birthday:"19950101",workCompany:"",job:"",relationship:""},
		trainForm:{trainName:"",company:"",certificate:"",timeSlot:""},
		//{trainName:"",company:"",certificate:"",timeSlot:"2013-2017"},
		workExperienceForm:{timeSlot:"",workCompany:"",job:"",reasons:"",pay:"",consultantName:"",consultantNum:""},
		//{timeSlot:"2013-2017",workCompany:"",job:"",reasons:"",pay:"",consultantName:"",consultantNum:"18314345458"},
        consultantForm:{name:"",workCompany:"",job:"",phoneNum:''},
        //{name:"",workCompany:"",job:"",phoneNum:'18314345458'},
    }
  },
  props: ['parent','isShow','personId','index'],
	methods: {
	  onSubmit(){//
		this.$refs["personInfo"].validate((e) => {
          if (e) {
          	this.addPost();
          } else {
          	 this.$message.error('');
            return false;
          }
        });
	  },
	 addPost() {
		if(this.workExperience.length<=0){
      		console.log(this.workExperience.length);
			this.$message.error('');
        	return false;
      	}
		if(this.workExperience.length<=0){
      		console.log(this.workExperience.length);
			this.$message.error('');
        	return false;
      	}
		
	    this.form.education=JSON.stringify(this.education)
	    this.form.family=JSON.stringify(this.family)
	   	this.form.train=JSON.stringify(this.train)
	    this.form.consultant=JSON.stringify(this.consultant)
	    this.form.workExperience=JSON.stringify(this.workExperience)
	  	let url=this.$G.server+"/admin/addPersonInfo";
	  	this.$T.post(url,this.form,"",this.success);
	  },
	  success(res){
	  	if(res.code=="200"){
	  		console.log(res.data);
	  		this.getPersonId(res.data);
	  	}
	  },
	  getPersonId(id){
	  	console.log(this.index);
	  	let active=this.parent.paperList[this.index+1].tag;
	  	this.parent.active=active;
	  	this.parent.personId=id;
	  },
	  showModal(frame){
	  	switch(frame){
		  	case 'education':
		  		this.frame.education=true;
		  		break;
		  	case 'family':
		  		this.frame.family=true;
		  		break;
		  	case 'train':
		  		this.frame.train=true;
		  		break;
		  	case 'consultant':
		  		this.frame.consultant=true;
		  		break;
		  	case 'workExperience':
				this.frame.workExperience=true;
		  		break;
		  	
	  	}
	  },
	  cancel(){
	  	this.frame.consultant=false;
	  	this.frame.education=false;
	  	this.frame.family=false;
	  	this.frame.train=false;
	  	this.frame.workExperience=false;
	  },
	  deleteItem(index,frame){
	  	switch(frame){
		  	case 'education':
		  		this.education.splice(index,1);
		  		break;
		  	case 'family':
		  		this.family.splice(index,1);
		  		break;
		  	case 'train':
		  		this.train.splice(index,1);
		  		break;
		  	case 'consultant':
		  		this.consultant.splice(index,1);
		  		break;
		  	case 'workExperience':
				this.workExperience.splice(index,1);
		  		break;
	  	}
	  },
	  emptyFrame(obj){
	     for(let key in obj){
	         obj[key]  = ''
			}
	  },
	  addEducation(){
	  	let item=this.$G.clone(this.educationForm);
	  	this.education.push(item);
	  	this.cancel();
		this.emptyFrame(this.educationForm);
	  },
	  addFamily(){
	  	let item=this.$G.clone(this.familyForm);
	  	this.family.push(item);
	  	this.cancel();
		this.emptyFrame(this.familyForm);
	  },
	  addTrain(){
	  	let item=this.$G.clone(this.trainForm);
	  	this.train.push(item);
	  	this.cancel();
		this.emptyFrame(this.trainForm);
	  },
	  addWorkExperience(){
	  	let item=this.$G.clone(this.workExperienceForm);
	  	this.workExperience.push(item);
	  	this.cancel();
		this.emptyFrame(this.workExperienceForm);
	  },
	  addConsultant(){
	  	let item=this.$G.clone(this.consultantForm);
	  	this.consultant.push(item);
	  	this.cancel();
		this.emptyFrame(this.consultantForm);
	  },
	  getPersonInfo(id){
	  	let url=this.$G.server+"/admin/getPersonInfoById";
	  	this.$T.post(url,{personId:id},"",this.getInfoSuccess);
	  },
	  getInfoSuccess(res){
	  	if(res.code=="200"){
	  		if(res.code==200){
	  			this.form=res.data.form;
	  			this.family=res.data.family;
	  			this.education=res.data.education;
	  			this.train=res.data.train;
	  			this.workExperience=res.data.workExperience;
	  			this.consultant=res.data.consultant;
	  		}
	  	}
	  },
	},
	mounted() {
		if(this.personId!=''&&this.personId!=undefined){
			this.getPersonInfo(this.personId);
		}
		
	},
}
</script>

<style scoped>
.v-title1{
	text-align: center;padding: 10px 30px;color: lightskyblue;font-size: 22px;
}
.v-title2{
	text-align: left;padding: 10px 50px;color: lightskyblue;font-size: 18px;
}
</style>



