import Vue from 'vue'

const General= {};

let server='http://127.0.0.1:8080/solo';


General.goToByName=function(_this,_name){
	_this.$router.push({name: _name});
}
//
General.goTo=function(_this,_url,params){
	_this.$router.push({path: _url,query:params});
}
//
General.clone=function(obj){
    let temp = null;
    if(obj instanceof Array){
        temp = obj.concat();
    }else if(obj instanceof Function){
        //js
        temp = obj;
    }else{
        temp = new Object();
        for(let item in obj){
            let val = obj[item];
            temp[item] = typeof val == 'object'?clone(val):val; //
        }
    }
    return temp;
}
//
General.back=function(_this){
	_this.$router.back(-1);
}

//
 General.format=function(time, format) {
    var t = new Date(time);
    var tf = function (i) {
      return (i < 10 ? '0' : '') + i
    };
    return format.replace(/yyyy|MM|dd|HH|mm|ss/g, function (a) {
      switch (a) {
        case 'yyyy':
          return tf(t.getFullYear());
          break;
        case 'MM':
          return tf(t.getMonth() + 1);
          break;
        case 'mm':
          return tf(t.getMinutes());
          break;
        case 'dd':
          return tf(t.getDate());
          break;
        case 'HH':
          return tf(t.getHours());
          break;
        case 'ss':
          return tf(t.getSeconds());
          break;
      }
    })
  },

//
General.getAge=function(birthday)
{
    // 
    var birthDayTime = new Date(birthday).getTime();
    // 
    var nowTime = new Date().getTime();
    //(365 * 86400000 = 31536000000)
    return Math.ceil((nowTime-birthDayTime)/31536000000);
}


General.server=server;
export default General;



