<template>
  <div>
  	<div><button>{{dateTime}}</button></div>
  	<div><button >{{userInfo.userName}}</button><button @click="login"></button></div>
  </div>
</template>

<script>

//mapState  vuexStoreState()
//mapGetters   vuexStoreGetters(state)
//mapActions   vuexStoreActions(Getters)
import { mapState,mapActions} from 'vuex'
import Util from '@/util/my/util'

export default {
  name: 'Vuex',
  data () {
    return {
    
    }
  },
  //
  computed:{
  	...mapState(['userInfo']),
  	dateTime:function(){
			let time=new Date().Format("yyyy-MM-dd hh:mm:ss");
  		return time;
  	}
  },
  methods:{
			...mapActions(['setUserInfo']),
			login(){
				let userInfo={
							userId:'110',
							userName:'',
				}
				this.setUserInfo(userInfo);
			}
  },
  
}
</script>

<style>
</style>



