<template>
	<!--<div style="margin-top: 30px;overflow-y: scroll;height: 460px;">-->
	<content>
		<!--<el-button @click="messageBox" type="primary" icon="el-icon-edit" circle></el-button>-->
		<!--<el-collapse>
			<el-collapse-item v-for="(item,index) in groupings" :title="item" :name="index" :key="index">
			</el-collapse-item>

		</el-collapse>-->
		<el-autocomplete v-model="state4" :fetch-suggestions="querySearchAsync" placeholder="" @select="handleSelect" suffix-icon="el-icon-search"></el-autocomplete>

	</content>
	<!--</div>-->
</template>

<script>
	import Tool from '@/util/tool';
	import Common from '../../interface/common'
	import axios from 'axios'
	export default {
		name: 'chat',
		data() {
			return {
				friends: [1, 2, 3, 4, 5, 6, 7, 8, 9, 0],
				groupings: ['', '1', '2', '3'],
				restaurants: [],
				state4: '',
				timeout: null
			}
		},
		props: ['parent'],
		computed: {

		},
		methods: {
			messageBox() {
				this.$prompt('', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					//inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
					inputValidator: (val) => {
						if(val.length > 11) {
							return '';
						}
						console.log(val);
						for(var i = 0; i < this.groupings.length; i++) {
							if(this.groupings[i] == val) {
								return '';
							}
						}
					},
					//inputErrorMessage: ''
				}).then(({
					value
				}) => {
					this.$message({
						type: 'success',
						message: ': ' + value
					});
					let param = {
						userId: '',
						groupName: value
					};
					Common.friendGroup.post(param, this);

				})
				//				.catch(() => {
				//					this.$message({
				//						type: 'info',
				//						message: ''
				//					});
				//				});
			},
			createGroupCallback(res) {
				console.log(res.code);
			},
			loadAll() {
				var url = "http://" + window.url + '/chat/friends';
				let list = [];
				axios.get(url).then(function(response) {
					for(let i of response.data.data) {
						i.value = i.username; //value
						list.push(i);
					}

				});
				return list;
			},
			//			loadAll() {
			//				return [{
			//						"value": "",
			//						"address": "144"
			//					},
			//					{
			//						"value": "Hot honey ",
			//						"address": "661"
			//					},
			//					{
			//						"value": "",
			//						"address": "9886113"
			//					},
			//					{
			//						"value": "()",
			//						"address": "438"
			//					},
			//					{
			//						"value": "",
			//						"address": "96811818-101"
			//					},
			//					{
			//						"value": "",
			//						"address": "633"
			//					},
			//				]
			//			},
			querySearchAsync(queryString, cb) {
				var restaurants = this.restaurants;
				var results = queryString ? restaurants.filter(this.createStateFilter(queryString)) : restaurants;
				clearTimeout(this.timeout);
				this.timeout = setTimeout(() => {
					cb(results);
				}, 3000 * Math.random());
			},
			createStateFilter(queryString) {
				return(state) => {
					return(state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
				};
			},
			handleSelect(item) {
				console.log(item);
				this.open2(item);
			},
			open2(data) {
				//console.log('friendsID' + data.id);
				this.$confirm('' + data.username + '', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					//					this.$message({
					//						type: 'success',
					//						message: '!'
					//					});
					this.$router.push({
						path: '/user/personalSpace',
						//						name: 'personalSpace',
						query: {
							userId: data.id
						}
					})
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					});
				});
			}

		},

		created() {},
		mounted() { //this.userId = Tool.getCookie('userId');
			this.restaurants = this.loadAll();
		},
	}
</script>

<style>
	/**/
	
	content::-webkit-scrollbar {
		width: 0px;
	}
	
	content::-webkit-scrollbar-thumb {
		border-radius: 5px;
		background: cornflowerblue;
	}
	
	content::-webkit-scrollbar-track {
		border-radius: 5px;
		background-color: #eee;
	}
</style>



