<template>
	<div>
		<div class="v-title1"></div>	
		<!---->
		<div style="text-align: center;">
			<el-form :inline="true" :model="page" class="demo-form-inline" style="padding: 0px 40px;">
			  </el-form-item>
			  <el-form-item label="">
			    <el-input v-model="page.name" placeholder=""></el-input>
			  </el-form-item>
			  <el-form-item label="">
			    <el-input v-model="page.sex" placeholder=""></el-input>
			  </el-form-item>
			  <el-form-item label="">
			    <el-input v-model="page.phoneNum" placeholder=""></el-input>
			  </el-form-item>
			  <el-form-item label="">
			    <el-input v-model="page.applyJob" placeholder=""></el-input>
			  </el-form-item>
			  <el-form-item>
			    <el-button type="primary" size="small" @click="getPage"></el-button>
			  </el-form-item>
			  <el-form-item>
			    <el-button type="info" size="small" @click="reset"></el-button>
			  </el-form-item>
			</el-form>
		</div>
	
		<!---->
		<div style="padding: 0px 20px;" >
		  <el-table :data="list"  ref="multipleTable"   @selection-change="selection" @row-dblclick="goShow">
			<el-table-column type="selection" width="55">
		    </el-table-column>
		    <el-table-column type="index" :index="indexMethod">
		    </el-table-column>
		    <el-table-column label="" prop="name" width="100" show-overflow-tooltip>
		    </el-table-column>
		    <el-table-column label="" prop="sex"  width="50" show-overflow-tooltip >
		    </el-table-column>
		    <el-table-column label="" prop="applyJob"  show-overflow-tooltip >
		    </el-table-column>
		    <el-table-column label=""   width="50" show-overflow-tooltip >
		      <template slot-scope="props">  
		      	{{$G.getAge(props.row.birthday)}}
		      </template>
		    </el-table-column>
		    <el-table-column label="" prop="phoneNum" width="80"  show-overflow-tooltip >
		    </el-table-column>
		    <el-table-column label="" prop="email" show-overflow-tooltip >
		    </el-table-column>
		    <el-table-column type="expand" label="" width="100px">
		      <template slot-scope="props">
		        <el-form label-position="left" inline class="demo-table-expand" >
		          <el-form-item label="">
		            <span>{{props.row.name}}</span>
		          </el-form-item>
		          <el-form-item label="">
		            <span> <el-tag :type="props.row.sex==''? '':'danger'" >{{props.row.sex}}</el-tag> </span>
		          </el-form-item>
		          <el-form-item label="">
		            <span> {{props.row.identityNum}} </span>
		          </el-form-item>
		          <el-form-item label="">
		            <span> {{props.row.identityNum}} </span>
		          </el-form-item>
		          <el-form-item label="">
		            <span> {{props.row.identityNum}} </span>
		          </el-form-item>
		          <el-form-item label="">
		            <span> {{props.row.isMarry}}</span>
		          </el-form-item>
		          <el-form-item label="">
		            <span>{{props.row.politicalIdentity}} </span>
		          </el-form-item>
		          <el-form-item label="">
		            <span> {{props.row.speciality}} </span>
		          </el-form-item>
		          <el-form-item label="" >
		            <span>{{props.row.householdAddress}}</span>
		          </el-form-item>
		          <el-form-item label="" >
		            <span>{{props.row.applyJob}}</span>
		          </el-form-item>
		          <el-form-item label="" >
		            <span>{{props.row.major}}</span>
		          </el-form-item>
		          <el-form-item label="" >
		            <span>{{props.row.phoneNum}}</span>
		          </el-form-item>
		          <el-form-item label="email" >
		            <span>{{props.row.identityNum}}</span>
		          </el-form-item>
		          <el-form-item label="" >
		            <span>{{props.row.address}}</span>
		          </el-form-item>
		          <el-form-item label="" >
		            <span>{{props.row.arrivalTime}}</span>
		          </el-form-item>
		          <br />
		        </el-form>
		      </template>
		    </el-table-column>
		    <el-table-column label=""  >
		      <template slot-scope="scope">  
		      	<el-button  @click="goShow(scope.row)" type="text" size="small"></el-button> 
		    	<el-button v-if="false" @click="handleClick(scope.row)" type="text" size="small"></el-button>   
		      </template>
		    </el-table-column>
		  </el-table>
		</div>
	  
		<div style="padding: 20px;">
		    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
		    	:current-page="page.page"  :page-size="page.pageSize" 	:total="total"
		      layout="total, sizes, prev, pager, next, jumper" :page-sizes="[10, 20, 50, 100]"
		      	>
		    </el-pagination>
		</div>
		
		<el-dialog title="" :visible.sync="showDetailed" width="80%"  top="10px" >
		  <div>
		  	<Main ref="MainDiv" :personIdMain="personId" v-if="showDetailed"></Main>
		  </div>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="showDetailed = false"> </el-button>
		    <el-button type="primary" @click="showDetailed = false"> </el-button>
		  </span>
		</el-dialog>

  </div>
</template>

<script>
import Main from "@/view/admin/main";
	
export default {
  name: 'admin',
  data () {
    return {
    	This:this,
    	
    	showDetailed:false,
    	personId:'',
    	
	    list: [],
	    total:0,
        page:{
			name:'',//
			sex:'',//
			identityNum:'',//
			nation:'',//
			birthday:'',//
			isMarry:'',//
			politicalIdentity:'',//
			speciality:'',//
			householdAddress:'',//
			householdType:'',//
			applyJob:'',//
			job:'',//
			major:'',//
			phoneNum:'',//
			email:'',//
			address:'',//
			height:'',//
			weight:'',//
			emergencyContact:'',//
			emergencyPhone:'',//
			inputTime:'',//
			img:'',//
			arrivalTime:'',//
        	page:1,
        	pageSize:10,
        	sortWord:'',
        	isAsc:1,
        },
		selectionList:[],
    }
  },
	components: {
		Main
	},
	methods: {
      indexMethod(index) {//index
        return index +1;
      },
	  handleClick(row) {//
	    console.log(row);
	  },
	  handleSizeChange(val){//
	  	this.page.pageSize=val;
	  	this.getPage();
	  },
	  handleCurrentChange(val){//
	  	this.page.page=val;
	  	this.getPage();
	  },
	  getPage(){//
	  	let url=this.$G.server+"/admin/getPersonPage";
	  	let data=this.page;
	  	this.$T.post(url,data,"",this.success);
	  },
	  success(res){ //
	  	if(res.code=="200"){
	  		this.list=res.data.data;
	  		this.total=res.data.total;
	  	}
	  },
	  reset(){//
	  	let page={
			name:'',//
			sex:'',//
			identityNum:'',//
			nation:'',//
			birthday:'',//
			isMarry:'',//
			politicalIdentity:'',//
			speciality:'',//
			householdAddress:'',//
			householdType:'',//
			applyJob:'',//
			job:'',//
			major:'',//
			phoneNum:'',//
			email:'',//
			address:'',//
			height:'',//
			weight:'',//
			emergencyContact:'',//
			emergencyPhone:'',//
			inputTime:'',//
			img:'',//
			arrivalTime:'',//
			//isAgree:true,//
        	page:1,
        	pageSize:10,
        	sortWord:'',
        	isAsc:1,
        }
	  	this.page=page
		this.getPage();
	  },
	  selection(val){//
		 this.selectionList = val;
	  },
	  clear(){//
		 this.$refs.multipleTable.clearSelection();
	  },
	  goShow(row){
		this.showDetailed=true;
		this.personId=row.id;
	  },

	  
	},
	mounted() {
		this.getPage();
	},
}
</script>

<style>
</style>



