<template>
  <div class="hello" style="text-align: center;">
    <h1>{{ msg }}</h1>
		
		<el-table :data="indexList" style="width: 60%;margin: 0px 20%;">

		    
		    <el-table-column	label=""	width="180" align='center'>
		      <template slot-scope="scope">
		        <el-popover trigger="hover" placement="left">
		          <p>src: {{ scope.row.src }}</p>
		          <p>msg: {{ scope.row.msg }}</p>
		          <div slot="reference" class="name-wrapper">
		            <span >{{ scope.row.name }}</span>
		          </div>
		        </el-popover>
		      </template>
		    </el-table-column>
		    
		    <el-table-column	label=""	width="180" align='center'>
		      <template slot-scope="scope">
		        <span style="margin-left: 10px" >{{ scope.row.title }}</span>
		      </template>
		    </el-table-column>
		    
		    <el-table-column label="" align='center'>
		      <template slot-scope="scope">
		        <el-button	size="mini"	type="success" @click="goTo(scope.row.src)"></el-button>
		        <!--<el-button	size="mini"	type="danger"	@click="handleDelete(scope.$index, scope.row)"></el-button>-->
		      </template>
		    </el-table-column>
		  </el-table>
	</div>
</template>

<script>
	export default {
		name: 'HelloWorld',
		data() {
			return {
				msg: 'Index',
				indexList:[
				{name:'',src:'/login',title:"Login",msg:''},
				{name:'',src:'/register',title:"Register",msg:''},
				{name:'',src:'/restore',title:"RestorePassword",msg:''},
				{name:'',src:'/changePassword',title:"ChangePassword",msg:''},
				{name:'',src:'/user/personalSpace',title:"PersonalSpace",msg:''},
				{name:'',src:'/user/personalCenter',title:"PersonalCenter",msg:''},
				{name:'',src:'/user/personalInfo',title:"PersonalInfo",msg:''},
				{name:'',src:'/user/ChangeHeadPhoto',title:"ChangeHeadPhoto",msg:''},
				{name:'',src:'/chat',title:"Chat",msg:''},
				],
			}
		},
		methods: {
			//
			goTo(url) {
				this.$router.push({
					path: url,
					query: {
					}
				});
			},
      handleDelete(index, row) {
        console.log(row);
        console.log(index);
      },

		},

	}
</script>

