import Vue from 'vue'
import Router from 'vue-router'

//

//
import header from '@/components/header'
//
import CalendarMonth from '@/components/calendar/month';



export default [
  	{
		path: '/header',
		name: '',
		component: header
	},
    {
      path: '/calendar/month',
      name: '',
      component: CalendarMonth
    }
  ]



