import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import getters from './getters'
import actions from './actions'

Vue.use(Vuex)

//
const state={
	userId:'',
	//
	userInfo:{

		userId:'-1',
		userName:'',

	},
	
	tonken:{
		
	},
	//webSocket
	chatSocket:'',
	websock:'',
	
	//
	
	//
	rememberUser:true,
}


//
export default new Vuex.Store({
	state,
	getters,
	actions,
	mutations,
})


